import React from 'react'
import { useState } from 'react'
import up from '../../../assets/up.svg'
import down from '../../../assets/down.svg'

export default function Dropdown(props) {
  // ici les props sont hÃ©ritÃ©es de la page Fichelogement ou Apropos
  // et sont : title="Description"; taille = 'petit' ou 'grand';
  // className = 'description__dropdown' et contenu(mappÃ© prÃ©cÃ©demment)
  // + initialisation d'une variable "d'Ã©tat" de la barre dropdown par dÃ©faut sur "false" (fermÃ©e)
  const [etat, setEtat] = useState(false)

  return (
    <>
      <article
        // attribution d'une classe diffÃ©rente selon que la barre dropdown a des propriÃ©tÃ©s
        // "taille" petite (dropdownsmall) ou grande (dropdownlarge)
        // hÃ©ritÃ©e  de la page auquelle elle fait rÃ©fÃ©rence.
        className={props.taille === 'petit' ? 'dropdownsmall' : 'dropdownlarge'}
      >
        <div>
          {/*  Evenement au clic de la barre dropdown (sous forme de boutton)
           -> Modification du usestate en l'inverse de l'Ã©tat actuel  */}
          <button className={props.className} onClick={() => setEtat(!etat)}>
            <h2 className="titre__dropdown">{props.title}</h2>
            {/*  Selon l'Ã©tat "true ou false" de la barre, 
            attribution d'une image diffÃ©rente de chevron (up ou down)*/}
            <img
              className="chevron"
              src={etat ? up : down}
              alt="chevron down"
            />
          </button>
        </div>
        {/*  Si l'Ã©tat de la barre est "true", Ajout d'une div de text ayant
        // le contenu appropriÃ© selon ses propriÃ©tÃ©s*/}
        {etat === true && (
          <div className="greyContainer">
            <ul className="contenu__dropdown">{props.contenu}</ul>
          </div>
        )}
      </article>
    </>
  )
}
